import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook, faInstagram, faTelegram } from '@fortawesome/free-brands-svg-icons';
import { Link } from 'react-router-dom';
import React from 'react';
import "../style.css";
import toggleMenu from "../App.js"
import { useState } from 'react';

const Header = () => {
  return (
    <header id='mainHeader'>
      <div className="container nav">
          <div className="burger">
            <button className="btn btn-primary" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">&#9776;</button>
            <div className="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
              <div className="offcanvas-body">
                <button type="button" class="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                <ul>
                  <li><a href="/">Sahifa</a></li>
                  &nbsp;
                  <li><a href="/products">Mahsulotlar</a></li>
                  &nbsp;
                  <li><a href="/about">Haqimizda</a></li>
                  &nbsp;
                  <li><a href="/connecting">Bog`lanish</a></li>
                </ul>
              </div>
            </div>
        </div>
        <div className="logo">
          <svg viewBox="0 0 64 64" xmlns="http://www.w3.org/2000/svg"><circle cx="32" cy="32" r="30" fill="var(--accent-light)"/><path d="M20 30c0-6 12-12 18-6s-2 14-8 14-10-4-10-8z" fill="#fff" opacity="0.95"/><path d="M36 18c4 2 8 6 8 10" stroke="#fff" stroke-width="2" fill="none" stroke-linecap="round" opacity="0.9"/></svg>
          <div className='logo_text'>
            <div className="brand">Kkchoy</div>
            <div>Tabiiy choy va o`simliklar</div>
          </div>
        </div>
        <nav id='navblock'>
          <ul>
            <li><Link to="/">Bosh sahifa</Link></li>
            <li><Link to="/products">Mahsulotlar</Link></li>
            <li><Link to="/about">Biz haqimizda</Link></li>
            <li><Link to="/connecting">Bog`lanish</Link></li>
          </ul>
        </nav>
        <div className="social_media">
          <a href="#"><FontAwesomeIcon href='#' className='smicon' icon={faTelegram} /></a>
          <a href="#"><FontAwesomeIcon href='#' className='smicon' icon={faInstagram} /></a>
          <a href="#"><FontAwesomeIcon href='#' className='smicon' icon={faFacebook} /></a>
        </div>
        <div className="number_container">
          <h1 className='contact_number1'>+998 XX XXX 57 37</h1>
          <h1 className='contact_number1'>+998 XX XXX 04 70</h1>
        </div>
      </div>
    </header>
  )
}

export default Header