import React from 'react'

const NavAbout = () => {
  return (
    <>
      <div className="p-about">
        <div className="about1">
          <h1>Ko`kchoy.uz  Har bir piyolada tabiiylik va tinchlik</h1>
          <p>Ko`kchoy.uz  bu nafaqat choy do`koni, balki har bir mijozga tinchlik, sog`lomlik va tabiiy lazzat ulashuvchi maskan.
          Bizning asosiy maqsadimiz  O`zbekistonning qadimiy choy madaniyatini zamonaviy uslubda qayta tiklash va har bir insonni sifatli, tabiiy choy bilan tanishtirishdir.</p>
        </div>
        <div className="about2">
          <h1> Bizning choylar</h1>
          <p className='about2-p1'>Yashil choylar  tetiklik va salomatlik manbai;</p>
          <p>Qora choylar  klassik lazzat va iliqlik timsoli;</p>
          <p>Oqs choylar  nozik ta`mni qadrlovchilar uchun;</p>
          <p>O`simlikli aralashmalar  tabiiy o`tlar, limon, yalpiz va boshqa foydali komponentlardan iborat choylarni topasiz.</p>
        </div>
        <div className="about3">
          <h1> Bizning qadriyatlarimiz</h1>
          <h6>Ko`kchoy.uz har bir choy donasiga mehr bilan yondashadi.
          Biz uchun eng muhim narsa  mijozning ishonchi va rohatlanishi.</h6>
          <h5 className='about3-p'>Shuning uchun ham mahsulotlarimiz:</h5>
          <p>sun`iy rang va lazzat beruvchisiz,</p>
          <p>tabiiy tarzda quritilgan,</p>
          <p>xalqaro sifat standartlariga mos.</p>
        </div>
        <div className="about4">
          <h1> Missiyamiz</h1>
          <p className='about4-p'>Bizning missiyamiz  har bir oila dasturxonida tabiiy Ko`kchoy bo`lishini ta`minlash.</p>
          <p>Biz ishonamiz: bir piyola choy  bu shunchaki ichimlik emas, do`stlik, samimiyat va tinchlik belgisi.</p>
        </div>
      </div>
    </>
  )
}

export default NavAbout