import React from 'react'
import Coffee from './Images/Coffee.avif'

const Body = () => {
  return (
    <section className="hero">
      <div className="hero-div1">
        <h1>Kkchoy  har qadamda tabiat shifosi</h1>
        <p>Organik choy barglari, o`simlik aralashmalari va maxsus blendlar. Onlayn buyurtma yoki kafemizga tashrif!</p>
        <div class="search">
          <input placeholder="Nimani izlayapsiz? (Masalan: Yashil choy)" />
          <button className="btn">Qidirish</button>
        </div>
      </div>
      <div className="hero-div2">
        <img className='hero-img' src={Coffee} alt="tea"/>
      </div>
    </section>
)
}

export default Body