import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import {react, useEffect, useState} from "react";
import Header from "./pages/Header.jsx";
import Body from "./pages/Body.jsx";
import Products from "./pages/Products.jsx";
import About from "./pages/About.jsx";
import Bottom from "./pages/Bottom.jsx";
import NavProducts from "./pages/routes/NavProducts.jsx";
import NavAbout from "./pages/routes/NavAbout.jsx";
import navConnection from "./pages/routes/navConnection.jsx";
import { Link } from "react-router-dom";
import axios from "axios";

function App() {

  const [site, setSite] = useState("");

  useEffect(() =>{
    axios.get("http://127.0.0.1:8000/api/")
    .then(res => setSite(res.data.message))
    .catch(err => console.log(err));
  }, [])

  return (
    <>
    
      <Router>
        <Routes>
          <Route path="/" element={[<Header /> , <Body /> , <Products /> , <About /> , <Bottom /> ]}/>
          <Route path="/products" element={[<Header /> , <NavProducts /> , <Bottom />]} />
          <Route path="/about" element={[<Header /> ,<NavAbout /> , <Bottom />]} />
          <Route path="/connecting" element={[<Header /> ,<navConnection /> , <About />]} />
        </Routes>
      </Router>
    </>
  );
}

export default App;