import React from 'react';
import { tea } from '../../api/Database';
import { Link } from "react-router-dom";
import Item from './Item';

const NavProducts = () => {  
  return (
    <>
    <div className="p-header">
      <h1>Bizning mahsulotlar</h1>
    </div>
      <div className="p-product">
        {
          tea.map(function(pro){
            return <Item rasm={pro.image} nomi={pro.name} narxi={pro.cost}/>
          })
        }
      </div>
    </>
  )
}

export default NavProducts