import React, { useState } from 'react'

const About = () => {
    const [ value, setValue ] = useState('')

    const handleChange = (event) =>{
      setValue(event.target.value);
    }

    const handleClick = () =>{
      alert("Sizning xabaringiz muvaffaqiyatli jo'natildi!")
    }

  return (
    <>
        <section className="about" id="about">
          <div className='about-texts'>
              <h3>Biz haqimizda</h3>
              <p>Kkchoy  2024-yildan beri organik choy ishlab chiqaruvchi brend. Bizning maqsad  soglom hayot uchun tabiiy choyni ommalashtirish.</p>
          </div>
          <div className="card" style={{padding: "20px" , boxShadow: "var(--shadow)" , borderRadius: "14px"}}>
              <h4>Bog`lanish</h4>
              <form id="contact" onsubmit="event.preventDefault();alert('Xabar yuborildi!')">
                  <input value={value} onChange={handleChange} placeholder="Ismingiz" />
                  <input placeholder="Email" />
                  <textarea placeholder="Xabar" rows="4"></textarea>
                  <button to="/" onClick={handleClick} className="btn">Yuborish</button>
              </form>
          </div>
        </section>
    </>
  )
}

export default About;